import React, { useState, useEffect, useRef } from 'react';
import api from './api/axios';
import './MonitorSidebar.css';

const API_BASE = process.env.REACT_APP_API_BASE || "http://localhost:8000";

export default function MonitorSidebar({ markets, isOpen, onToggle, index, isAnyOpen, monitorData, onUpdate, onDelete }) {
    // Find initial index from city_target if possible
    const initialCityIdx = React.useMemo(() => {
        if (!monitorData?.city_target || !markets) return 0;
        const idx = markets.findIndex(m => m.city === monitorData.city_target);
        return idx !== -1 ? idx : 0;
    }, [monitorData?.city_target, markets]);

    const [selectedCityIdx, setSelectedCityIdx] = useState(initialCityIdx);
    const [monitorType, setMonitorType] = useState(monitorData?.type || 'cli');
    const [pollInterval, setPollInterval] = useState(monitorData?.poll_interval || 30);
    const [isPolling, setIsPolling] = useState(false); // Polling state intentionally does NOT persist across reloads; it always starts stopped.
    // Only the monitor configuration (city_target, type, poll_interval) is persisted via onUpdate/DB.
    // To start polling, the user must explicitly enable it each time the page is opened.
    
    const [logs, setLogs] = useState([]);
    const [previewContent, setPreviewContent] = useState(null);
    const [previousPreviewContent, setPreviousPreviewContent] = useState(null);
    const [hasChange, setHasChange] = useState(false);
    
    // Sync changes to parent/DB
    useEffect(() => {
        if (!onUpdate || !monitorData || !markets || markets.length === 0) return;
        
        const city = markets[selectedCityIdx] ? markets[selectedCityIdx].city : "";
        
        // Debounce or just check diff?
        if (
            city !== monitorData.city_target || 
            monitorType !== monitorData.type || 
            pollInterval !== monitorData.poll_interval
        ) {
             const timer = setTimeout(() => {
                 onUpdate({
                     ...monitorData,
                     city_target: city,
                     type: monitorType,
                     poll_interval: pollInterval
                 });
             }, 1000);
             return () => clearTimeout(timer);
        }
    }, [selectedCityIdx, monitorType, pollInterval, markets, monitorData, onUpdate]);

    const lastDataRef = useRef(null);
    const savedCheckUrl = useRef();
    const pollCountRef = useRef(0);
    const requestIdCounter = useRef(0);
    const lastProcessedRequestId = useRef(0);
    const audioCtxRef = useRef(null);


    const addLog = React.useCallback((msg, type = 'info') => {
        const time = new Date().toLocaleTimeString();
        const entry = { msg, time, type, id: Date.now() + Math.random() };

        setLogs(prev => {
            // Only overwrite if it's a poll AND the previous one was a poll
            if (type === 'poll' && prev.length > 0 && prev[0].type === 'poll') {
                return [entry, ...prev.slice(1)];
            }
            return [entry, ...prev].slice(0, 50);
        });
    }, []);

    const getTargetUrl = React.useCallback(() => {
        if (!markets || markets.length === 0) return '';
        const cityData = markets[selectedCityIdx];
        if (!cityData) return '';
        
        if (monitorType === 'cli') {
            const site = cityData.wfo_ticker?.substring(1) || ''; // e.g., KMFL -> MFL
            const issuedBy = cityData.station_id?.substring(1) || ''; // e.g., KMIA -> MIA
            return `https://forecast.weather.gov/product.php?site=${site}&product=CLI&issuedby=${issuedBy}`;
        } else {
            const stationId = cityData.station_id || '';
            if (!stationId) return '';
            return `https://www.weather.gov/wrh/timeseries?site=${stationId}`;
        }
    }, [markets, selectedCityIdx, monitorType]);

    const extractCliBlock = (text) => {
        if (!text) return "No content";
        const lines = text.split('\n');
        // Find TEMPERATURE line
        const startIdx = lines.findIndex(l => l.includes('TEMPERATURE'));
        if (startIdx !== -1) {
            // Find end index (PRECIPITATION) after start
            const endIdx = lines.findIndex((l, i) => i > startIdx && l.includes('PRECIPITATION'));
            
            // If not found, fallback to ~10 lines after TEMPERATURE
            const sliceEndIdx = endIdx === -1 ? startIdx + 10 : endIdx;
            
            const rawBlock = lines.slice(startIdx, sliceEndIdx);
            
            // Fixed headers for context
            const headers = "WEATHER ITEM OBS VAL TIME";
            
            // Truncate logic: Keep only first ~32 chars of each line (covers Max/Time/Record)
            // 38 chars was including Year which user said is 1 extra column too many
            const content = rawBlock.map(line => line.substring(0, 32).trimEnd()).join('\n').trimEnd();
            
            return headers + "\n" + content;
        }
        // Fallback: just return first 10 lines
        return lines.slice(0, 10).join('\n');
    };

    const getCompareValue = React.useCallback((result) => {
        if (monitorType === 'cli') {
             // For CLI, we care if the full text changes (content update)
             // or specifically timestamp. full_text covers all cases.
             return result.data?.full_text || '';
        } else {
             // For Timeseries, only care about LATEST observation
             // Ignore 'count' or 'keys' changes
             return JSON.stringify(result.latest);
        }
    }, [monitorType]);

    const getPreviewData = React.useCallback((result) => {
        if (monitorType === 'cli') {
            const ts = result.data?.timestamp || 'Unknown';
            const block = extractCliBlock(result.data?.full_text);
            return {
                title: "CLI Report",
                timestamp: ts,
                body: block
            };
        } else {
            const obs = result.latest || {};
            let ts = obs.timestamp || 'None';
            // Try to make ISO string more readable
            try {
                const date = new Date(ts);
                if (!isNaN(date.getTime())) {
                    // Use UTC first, then adjust? 
                    // No, new Date(ts) creates a date object in browser local time (if ts has offset)
                    // or UTC (if Z).
                    // toLocaleString with timeZone will convert that instant to the target zone.
                    
                    const tz = markets && markets[selectedCityIdx] ? markets[selectedCityIdx].timezone : undefined;
                    
                    // If ts comes from Synoptic, it might be ISO with offset (e.g. -0500)
                    // If so, Date() parses correctly.
                    
                    ts = date.toLocaleString('en-US', {
                        month: 'short', day: 'numeric',
                        hour: '2-digit', minute: '2-digit', second: '2-digit',
                        hour12: false,
                        timeZone: tz
                    });
                    
                    // Append tz name for clarity
                    if (tz) {
                        // short tz name is hard in JS API without libraries, 
                        // but we can append the IANA zone name if needed, or just leave it implied by context
                        // User asked for "source timezone".
                    }
                }
            } catch (e) {
                // If timestamp parsing fails, keep the original string but log for debugging.
                console.debug('Failed to parse observation timestamp', ts, e);
            }

            const padKey = (k) => (k + ':').padEnd(11, ' ');
            let body = [];

            if (obs.temp) {
                let displayTemp = obs.temp;
                const val = parseFloat(obs.temp);
                if (!isNaN(val)) {
                    const extra = String(obs.temp).includes('[') ? ' ' + String(obs.temp).substring(String(obs.temp).indexOf('[')) : '';
                    displayTemp = `${val.toFixed(1)}F${extra}`;
                }
                body.push(`${padKey('Temp')}${displayTemp}`);
            }
            
            if (obs.dewpoint)   body.push(`${padKey('Dewpoint')}${Number(obs.dewpoint).toFixed(1)}F`);
            if (obs.rh)         body.push(`${padKey('Humidity')}${Number(obs.rh).toFixed(0)}%`);
            
            if (obs.wind_speed) {
                const speed = Number(obs.wind_speed).toFixed(0);
                const dir = obs.wind_dir ? ` @ ${Number(obs.wind_dir).toFixed(0)}` : '';
                body.push(`${padKey('Wind')}${speed} mph${dir}`);
            }

            if (obs.max_6hr)    body.push(`${padKey('6hr Max')}${Number(obs.max_6hr).toFixed(1)}F`);
            if (obs.min_6hr)    body.push(`${padKey('6hr Min')}${Number(obs.min_6hr).toFixed(1)}F`);
            if (obs.max_24hr)   body.push(`${padKey('24hr Max')}${Number(obs.max_24hr).toFixed(1)}F`);
            if (obs.min_24hr)   body.push(`${padKey('24hr Min')}${Number(obs.min_24hr).toFixed(1)}F`);
            
            if (body.length === 0) body.push("No data available");

            return {
                title: "Latest Observation",
                timestamp: ts,
                body: body.join('\n')
            };
        }
    }, [monitorType, selectedCityIdx, markets]); // Dependencies for getPreviewData

    const updatePreview = React.useCallback((result) => {
        const newData = getPreviewData(result);
        setPreviewContent(newData);
    }, [getPreviewData]);

    const playChime = React.useCallback(() => {
        try {
            if (!audioCtxRef.current) return;
            const ctx = audioCtxRef.current;
            
            const now = ctx.currentTime;
            
            // Simple ascending chime
            [523.25, 659.25, 783.99, 1046.50].forEach((freq, i) => {
                const osc = ctx.createOscillator();
                const gain = ctx.createGain();
                osc.connect(gain);
                gain.connect(ctx.destination);
                
                osc.type = 'sine';
                osc.frequency.value = freq;
                
                const startTime = now + i * 0.1;
                gain.gain.setValueAtTime(0, startTime);
                gain.gain.linearRampToValueAtTime(0.1, startTime + 0.05);
                gain.gain.exponentialRampToValueAtTime(0.001, startTime + 1.0);
                
                osc.start(startTime);
                osc.stop(startTime + 1.0);
            });
        } catch (e) {
            console.error("Audio play failed", e);
        }
    }, []);

    const checkUrl = React.useCallback(async () => {
        const url = getTargetUrl();
        if (!url) return;
        
        // Increment request ID
        requestIdCounter.current += 1;
        const currentRequestId = requestIdCounter.current;

        try {
            const res = await api.post('/api/monitor/check', { url, type: monitorType });
            
            // Discard response if a newer request has already been processed
            // This prevents race conditions where a slow previous request overwrites a fast newer request
            if (currentRequestId <= lastProcessedRequestId.current) {
                console.debug(`Ignoring stale response (ReqID: ${currentRequestId} <= LastProcessed: ${lastProcessedRequestId.current})`);
                return;
            }
            lastProcessedRequestId.current = currentRequestId;

            const result = res.data;
            
            if (result.error) {
                addLog(`Error: ${result.error}`);
                return;
            }

            // Compare with last data using smart logic
            const currentVal = getCompareValue(result);
            
            if (lastDataRef.current && lastDataRef.current !== currentVal) {
                setHasChange(true);
                addLog(` CHANGE DETECTED!`, 'alert');
                playChime();
                
                // Save previous content before updating
                setPreviousPreviewContent(previewContent);
                
                if (monitorType === 'cli') {
                    const ts = result.data?.timestamp || 'Unknown time';
                    addLog(`New timestamp: ${ts}`, 'alert');
                } else {
                    const obs = result.latest || {};
                    addLog(`New Obs: ${obs.timestamp} (${obs.temp}F)`, 'alert');
                }
            } else if (!lastDataRef.current) {
                addLog(`Initial fetch successful.`);
            } else {
                pollCountRef.current += 1;
                addLog(`Successful poll #${pollCountRef.current}, no change.`, 'poll');
            }
            
            lastDataRef.current = currentVal;
            updatePreview(result);

        } catch (err) {
            addLog(`Fetch error: ${err.message}`, 'error');
        }
    }, [getTargetUrl, monitorType, getCompareValue, updatePreview, addLog, previewContent, playChime]);

    useEffect(() => {
        savedCheckUrl.current = checkUrl;
    }, [checkUrl]);

    useEffect(() => {
        if (isPolling) {
            // Call immediately? No, we call checkUrl manually on start in togglePolling
            // Just set interval
            const id = setInterval(() => {
                if (savedCheckUrl.current) savedCheckUrl.current();
            }, pollInterval * 1000);
            return () => clearInterval(id);
        }
    }, [isPolling, pollInterval]);

    const togglePolling = () => {
        if (isPolling) {
            setIsPolling(false);
            addLog("Stopped polling.");
            lastDataRef.current = null; 
        } else {
            setHasChange(false);
            setPreviousPreviewContent(null);
            lastDataRef.current = null;
            pollCountRef.current = 0;
            // Reset request tracking on restart
            // We don't strictly need to reset counters, but it's cleaner.
            // Just ensure we don't block new requests. 
            // Actually, we shouldn't reset requestIdCounter to 0 if we don't reset lastProcessedRequestId
            // Safest is just to leave them or increment.
            // Let's just leave them alone, the logic holds as long as they grow.
            
            // Init audio context on user gesture to bypass autoplay policy
            const AudioContext = window.AudioContext || window.webkitAudioContext;
            if (AudioContext) {
                if (!audioCtxRef.current) {
                    audioCtxRef.current = new AudioContext();
                }
                if (audioCtxRef.current.state === 'suspended') {
                    audioCtxRef.current.resume();
                }
            }

            addLog(`Starting polling (${pollInterval}s)...`);
            checkUrl(); // Initial check
            setIsPolling(true);
        }
    };

    // Cleanup on unmount (only interval cleared by useEffect)
    // useEffect(() => { return () => clearInterval(intervalRef.current); }, []);

    // Indicator class
    let indicatorClass = "indicator-light";
    if (hasChange) indicatorClass += " alert";
    else if (isPolling) indicatorClass += " active";

    // Determine sidebar classes
    // If THIS sidebar is open, it is 'exposed' and 'open'
    // If ANY sidebar is open, THIS sidebar is 'exposed' (to show toggle on left) but NOT 'open' (content hidden)
    // If NO sidebar is open, it is neither (hidden offscreen right)
    
    let containerClass = "monitor-sidebar";
    if (isAnyOpen) containerClass += " exposed";
    if (isOpen) containerClass += " open";

    return (
        <div className={containerClass}>
            <div 
                className="monitor-toggle" 
                onClick={onToggle} 
                title="Toggle Monitor"
                style={{ 
                    top: `${20 + (index * 60)}px`
                }}
            >
                <div className={indicatorClass}></div>
            </div>
            
            <div className="sidebar-content">
                <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '15px'}}>
                    <h2 style={{margin: 0, fontSize: '1.2rem'}}>Targeted Monitor</h2>
                    {onDelete && (
                        <button 
                            onClick={() => onDelete(monitorData.id)}
                            style={{
                                background: 'transparent',
                                border: '1px solid #ff4d4f',
                                color: '#ff4d4f',
                                borderRadius: '4px',
                                padding: '4px 8px',
                                cursor: 'pointer',
                                fontSize: '0.8rem'
                            }}
                            title="Delete Monitor"
                        >
                            Delete
                        </button>
                    )}
                </div>
                
                <div className="control-group">
                    <label>Target City</label>
                    <select 
                        value={selectedCityIdx} 
                        onChange={(e) => setSelectedCityIdx(Number(e.target.value))}
                        disabled={isPolling}
                    >
                        {markets && markets.map((m, i) => (
                            <option key={i} value={i}>{m.city} ({m.kalshi_ticker})</option>
                        ))}
                    </select>
                </div>

                <div className="control-group">
                    <label>Data Source</label>
                    <select 
                        value={monitorType} 
                        onChange={(e) => setMonitorType(e.target.value)}
                        disabled={isPolling}
                    >
                        <option value="cli">NWS CLI Report (Text)</option>
                        <option value="timeseries">NWS Timeseries (Obs)</option>
                    </select>
                </div>

                <div className="control-group">
                    <label>Poll Interval (seconds)</label>
                    <input 
                        type="number" 
                        min="5" 
                        value={pollInterval} 
                        onChange={(e) => setPollInterval(parseInt(e.target.value, 10))}
                        disabled={isPolling}
                    />
                </div>

                <div className="control-group">
                    <label>Target URL</label>
                    <div style={{fontSize: '0.8em', color: '#888', wordBreak: 'break-all'}}>
                        <a 
                            href={getTargetUrl()} 
                            target="_blank" 
                            rel="noopener noreferrer"
                            style={{color: '#4fa6ff', textDecoration: 'none'}}
                        >
                            {getTargetUrl()}
                        </a>
                    </div>
                </div>

                <button 
                    className={`btn-primary ${isPolling ? 'btn-danger' : ''}`}
                    onClick={togglePolling}
                >
                    {isPolling ? 'STOP MONITORING' : 'START MONITORING'}
                </button>

                {hasChange && (
                    <button 
                        className="btn-primary" 
                        style={{marginTop: '10px', backgroundColor: '#e6a700'}}
                        onClick={() => {
                            setHasChange(false);
                            setPreviousPreviewContent(null);
                        }}
                    >
                        ACKNOWLEDGE ALERT
                    </button>
                )}

                {hasChange && previousPreviewContent ? (
                    <div style={{marginTop: '20px'}}>
                        <div style={{color: '#888', fontSize: '0.8em', marginBottom: '5px', textTransform: 'uppercase', letterSpacing: '1px'}}>Change Detected</div>
                        
                        <div style={{display: 'flex', gap: '10px'}}>
                            <div style={{flex: 1, border: '1px solid #444', padding: '10px', backgroundColor: '#2a2a2a', opacity: 0.7, minWidth: 0}}>
                                <div style={{fontWeight: 'bold', color: '#ff6b6b', fontSize: '0.9em'}}>PREVIOUS</div>
                                <div style={{fontSize: '0.8em', color: '#999', marginBottom: '5px', whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis'}}>{previousPreviewContent.timestamp}</div>
                                <pre style={{margin: 0, whiteSpace: 'pre', overflowX: 'auto', fontSize: '0.7em', color: '#ccc', fontFamily: 'monospace'}}>
                                    {previousPreviewContent.body}
                                </pre>
                            </div>

                            <div style={{flex: 1, border: '1px solid #444', padding: '10px', backgroundColor: '#2a2a2a', minWidth: 0}}>
                                <div style={{fontWeight: 'bold', color: '#51cf66', fontSize: '0.9em'}}>CURRENT</div>
                                <div style={{fontSize: '0.8em', color: '#999', marginBottom: '5px', whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis'}}>{previewContent?.timestamp}</div>
                                <pre style={{margin: 0, whiteSpace: 'pre', overflowX: 'auto', fontSize: '0.7em', color: '#ccc', fontFamily: 'monospace'}}>
                                    {previewContent?.body}
                                </pre>
                            </div>
                        </div>
                    </div>
                ) : (
                    previewContent && (
                        <div style={{marginTop: '20px', border: '1px solid #444', borderRadius: '4px', overflow: 'hidden'}}>
                            <div style={{backgroundColor: '#333', padding: '8px 10px', display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
                                <span style={{fontWeight: 'bold', fontSize: '0.9em'}}>{previewContent.title}</span>
                                <span style={{fontSize: '0.8em', color: '#aaa'}}>{previewContent.timestamp}</span>
                            </div>
                            <div style={{padding: '10px', backgroundColor: '#222'}}>
                                <pre style={{margin: 0, whiteSpace: 'pre', overflowX: 'auto', fontSize: '0.85em', color: '#eee', fontFamily: 'monospace', lineHeight: '1.4'}}>
                                    {previewContent.body}
                                </pre>
                            </div>
                        </div>
                    )
                )}

                <div className="logs-panel">
                    {logs.map((log) => (
                        <div key={log.id} className={`log-entry ${log.type}`}>
                            <span className="log-time">[{log.time}]</span>
                            {log.msg}
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}
