import React, { useEffect, useState, useContext, useCallback } from 'react';
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, ReferenceLine, ReferenceArea, Brush
} from 'recharts';
import {
  DndContext, 
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  rectSortingStrategy,
  useSortable
} from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import './App.css';
import MonitorSidebar from './MonitorSidebar';
import { AuthProvider, AuthContext } from './context/AuthContext';
import Auth from './components/Auth';
import api from './api/axios';
import NWS_MAPPING from './static/nws_mapping.json';

const API_BASE = process.env.REACT_APP_API_BASE || "http://localhost:8000";

function CityCard({ cityData, viewMode, dragHandleProps, onRefresh }) {
  const [forecast, setForecast] = useState(null);
  const [kalshiForecast, setKalshiForecast] = useState(null);
  const [brushIndexes, setBrushIndexes] = useState({ startIndex: 0, endIndex: 0 });
  const [isRefreshing, setIsRefreshing] = useState(false);

  const handleRefresh = async () => {
      setIsRefreshing(true);
      if (onRefresh) {
          await onRefresh(cityData.city);
      }
      setIsRefreshing(false);
  };

  useEffect(() => {
    if (cityData.city) {
        // Fetch Model Forecasts
        fetch(`${API_BASE}/api/weather/forecast/hourly?city=${cityData.city}`)
            .then(res => res.json())
            .then(data => setForecast(data))
            .catch(err => console.error(err));
            
        // Fetch Kalshi Implied Forecasts
        fetch(`${API_BASE}/api/kalshi/forecast?city=${cityData.city}`)
            .then(res => res.json())
            .then(data => setKalshiForecast(data))
            .catch(err => console.error(err));
    }
  }, [cityData.city]);

  // Process Data with Memoization to avoid re-runs
  const processedData = React.useMemo(() => {
       if (cityData.error) return null;
       if (cityData.loading || !cityData.today_midnight) return null;

       const midnightTime = new Date(cityData.today_midnight).getTime();
       const startTime = new Date(cityData.history_start).getTime();
       const startOfTomorrow = midnightTime + 24 * 60 * 60 * 1000;
       const endOfTomorrow = midnightTime + 48 * 60 * 60 * 1000;

       // Parse Kalshi Reference Lines
       const kalshiRefLines = [];
       if (kalshiForecast && kalshiForecast.forecasts) {
           Object.entries(kalshiForecast.forecasts).forEach(([dateStr, vals]) => {
               const todayStr = cityData.today_midnight.split('T')[0];
               const tDate = new Date(cityData.today_midnight);
               tDate.setDate(tDate.getDate() + 1);
               const tomorrowStr = tDate.toISOString().split('T')[0];
               
               let startX, endX;
               if (dateStr === todayStr) {
                   startX = midnightTime;
                   endX = startOfTomorrow;
               } else if (dateStr === tomorrowStr) {
                   startX = startOfTomorrow;
                   endX = endOfTomorrow;
               } else {
                   return;
               }
               
               const processForecasts = (list, singleVal, singleProb, color, labelPrefix) => {
                   if (list && list.length > 0) {
                        const maxProb = Math.max(...list.map(i => i.prob));
                        list.forEach(item => {
                             const isMain = item.prob === maxProb;
                             kalshiRefLines.push({
                                 segment: [{ x: startX, y: item.val }, { x: endX, y: item.val }],
                                 stroke: color,
                                 strokeOpacity: isMain ? 1 : 0.3,
                                 strokeWidth: isMain ? 2 : 1,
                                 strokeDasharray: isMain ? "3 3" : "2 2",
                                 label: isMain 
                                      ? `${labelPrefix}: ${item.val.toFixed(1)} (${item.prob.toFixed(0)}%)`
                                      : `${item.prob.toFixed(0)}%`,
                                 labelSize: isMain ? 11 : 9,
                                 labelPos: 'insideTopRight',
                                 labelColor: color
                             });
                        });
                   } else if (singleVal !== undefined) {
                       kalshiRefLines.push({
                           segment: [{ x: startX, y: singleVal }, { x: endX, y: singleVal }],
                           stroke: color,
                           strokeOpacity: 1,
                           strokeWidth: 2,
                           strokeDasharray: "3 3",
                           label: `${labelPrefix}: ${singleVal.toFixed(1)} (${singleProb ? singleProb.toFixed(0) : '-'}%)`,
                           labelSize: 11,
                           labelPos: 'insideTopRight',
                           labelColor: color
                       });
                   }
               };

               processForecasts(vals.highs, vals.high, vals.high_prob, "#ff4d4f", "Kalshi High");
               processForecasts(vals.lows, vals.low, vals.low_prob, "#40a9ff", "Kalshi Low");

           });
       }

       const historyData = cityData.data.map(pt => ({
            time: new Date(pt.timestamp).getTime(),
            actual: pt.temp_f,
            type: 'history'
        }));

        let forecastDataArray = [];
        let modelKeys = [];
        if (forecast && forecast.data) {
             modelKeys = forecast.models || [];
             forecastDataArray = forecast.data.map(pt => {
                 const item = { time: new Date(pt.timestamp).getTime(), type: 'forecast' };
                 modelKeys.forEach(m => item[m] = pt[m]);
                 return item;
             });
        }

        let merged = [...historyData, ...forecastDataArray].sort((a,b) => a.time - b.time);
        merged = merged.filter(d => d.time <= endOfTomorrow);
        
        if (modelKeys.length > 0 && forecastDataArray.length > 0) {
             const anchors = forecastDataArray.sort((a,b) => a.time - b.time);
             merged.forEach(pt => {
                if(pt.type === 'forecast') return;
                let nextIdx = anchors.findIndex(a => a.time > pt.time);
                if (nextIdx > 0 && nextIdx !== -1) {
                    const prev = anchors[nextIdx - 1];
                    const next = anchors[nextIdx];
                    const alpha = (pt.time - prev.time) / (next.time - prev.time);
                    modelKeys.forEach(m => {
                         if (prev[m] !== undefined && next[m] !== undefined) {
                              pt[m] = prev[m] + alpha * (next[m] - prev[m]);
                         }
                    });
                }
             });
        }

        if (merged.length > 0 && merged[0].time > startTime) {
            merged.unshift({ time: startTime, actual: null });
        }

        let minT = 1000, maxT = -1000;
        let latestActual = '-';
        let latestActualValue = null;
        if (historyData.length > 0) {
             latestActualValue = historyData[historyData.length - 1].actual;
             latestActual = latestActualValue.toFixed(1);
        }

        merged.forEach(d => {
             if (d.actual !== undefined && d.actual !== null) { minT = Math.min(minT, d.actual); maxT = Math.max(maxT, d.actual); }
             modelKeys.forEach(m => { if(d[m] !== undefined && d[m] !== null) { minT = Math.min(minT, d[m]); maxT = Math.max(maxT, d[m]); } });
        });
        
        if (minT === 1000) minT = 0; 
        if (maxT === -1000) maxT = 100;
        minT = Math.floor(minT - 2);
        maxT = Math.ceil(maxT + 2);

        return {
           allData: merged,
           models: modelKeys,
           minTemp: minT,
           maxTemp: maxT,
           midnightTime,
           startOfTomorrow,
           startTime,
           latestActual,
           latestActualValue,
           kalshiRefLines
        };
  }, [cityData, forecast, kalshiForecast]);

  useEffect(() => {
      if (!processedData) return;
      const { allData, midnightTime } = processedData;
      if (!allData || allData.length === 0) return;

      let startT, endT;
      const oneDay = 24 * 60 * 60 * 1000;
      
      if (viewMode === 'today') {
          startT = midnightTime;
          endT = midnightTime + oneDay;
      } else if (viewMode === 'tomorrow') {
          startT = midnightTime;
          endT = midnightTime + 2 * oneDay;
      } else {
          setBrushIndexes({ startIndex: 0, endIndex: allData.length - 1 });
          return;
      }
      
      let sIdx = allData.findIndex(d => d.time >= startT);
      if (sIdx === -1) sIdx = 0;
      
      let eIdx = allData.findIndex(d => d.time > endT);
      if (eIdx === -1) eIdx = allData.length - 1;
      else eIdx = Math.max(0, eIdx - 1);
      
      setBrushIndexes({ startIndex: sIdx, endIndex: eIdx });

  }, [viewMode, processedData]);

  const visibleKalshiLines = React.useMemo(() => {
    if (!processedData || !processedData.kalshiRefLines || !processedData.allData) return [];
    
    const { allData, kalshiRefLines } = processedData;
    if (allData.length === 0) return [];

    const startIdx = brushIndexes.startIndex;
    const endIdx = brushIndexes.endIndex;
    
    if (startIdx >= allData.length || endIdx >= allData.length) return kalshiRefLines;
    
    const visibleStart = allData[startIdx].time;
    const visibleEnd = allData[endIdx].time;

    return kalshiRefLines.map(ref => {
      const lineStart = ref.segment[0].x;
      const lineEnd = ref.segment[1].x;
      const yValue = ref.segment[0].y;
      const clippedStart = Math.max(lineStart, visibleStart);
      const clippedEnd = Math.min(lineEnd, visibleEnd);

      if (clippedStart >= clippedEnd) return null;

      return {
        ...ref,
        segment: [
          { x: clippedStart, y: yValue },
          { x: clippedEnd, y: yValue }
        ]
      };
    }).filter(line => line !== null);
  }, [processedData, brushIndexes]);

  if (cityData.error) {
    return (
      <div className="card error">
        <h3>{cityData.city}</h3>
        <p>Error: {cityData.error}</p>
      </div>
    );
  }

  // Handle loading/skeleton state
  if (cityData.loading || (!processedData && !cityData.data)) {
      return (
          <div className="card" style={{ height: '400px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
               <h3 style={{position: 'absolute', top: 20, left: 20, color: '#aaa'}}>{cityData.city}</h3>
               <div style={{color: '#888', fontStyle: 'italic'}}>Fetching weather data...</div>
          </div>
      );
  }

  if (!processedData) return null;

  const { allData, models, minTemp, maxTemp, midnightTime, startOfTomorrow, startTime, latestActual, latestActualValue } = processedData;

  const formatTick = (timeNum) => {
    const date = new Date(timeNum);
    return date.toLocaleTimeString('en-US', { 
      hour: '2-digit', 
      minute: '2-digit',
      hour12: false,
      timeZone: cityData.timezone 
    });
  };

  const nwsToCliRange = (val) => {
      const i = Math.round(val);
      const mapped = NWS_MAPPING[String(i)];
      return mapped ? `[${mapped[0]}, ${mapped[1]}]` : `[${i}]`;
  };

  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      return (
        <div className="custom-tooltip" style={{ backgroundColor: '#282c34', padding: '6px', border: '1px solid #555', fontSize: '12px' }}>
          <p className="label" style={{ margin: 0, marginBottom: '2px', color: '#aaa', fontSize: '11px' }}>{formatTick(label)}</p>
          {payload.map((p, i) => {
             let valStr = p.value !== undefined ? p.value.toFixed(1) : '-';
             if (p.name === 'Actual (NWS)' && p.value !== undefined && p.value !== null) {
                  valStr += ` ${nwsToCliRange(p.value)}`;
             }
             return (
              <p key={i} style={{ margin: 0, color: p.color, fontSize: '11px' }}>
                  {p.name}: {valStr}F
              </p>
             );
          })}
        </div>
      );
    }
    return null;
  };

  const modelColors = {
      ecmwf: "#ff7300",
      gfs: "#82ca9d",
      icon: "#8884d8",
      gem: "#ffc658"
  };

  return (
    <div className="card">
      <h3 style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <div style={{ display: 'flex', alignItems: 'center' }}>
            {dragHandleProps && (
                <span {...dragHandleProps.attributes} {...dragHandleProps.listeners} style={{ cursor: 'grab', marginRight: '10px', fontSize: '1.2rem', color: '#666' }}>
                    
                </span>
            )}
            <span>{cityData.city}</span>
            {cityData.kalshi_ticker && (
                <span style={{ fontSize: '0.7rem', marginLeft: '10px' }}>
                <a href={`https://kalshi.com/markets/${cityData.kalshi_ticker}`} target="_blank" rel="noopener noreferrer" style={{ color: '#ff4d4f', textDecoration: 'none', marginRight: '8px' }}>[high]</a>
                <a href={`https://kalshi.com/markets/${cityData.kalshi_ticker.replace('KXHIGH', 'KXLOWT')}`} target="_blank" rel="noopener noreferrer" style={{ color: '#40a9ff', textDecoration: 'none', marginRight: '8px' }}>[low]</a>
                <a href={`https://forecast.weather.gov/product.php?site=${cityData.wfo_ticker?.substring(1)}&product=CLI&issuedby=${cityData.station_id?.substring(1)}`} target="_blank" rel="noopener noreferrer" style={{ color: '#82ca9d', textDecoration: 'none', marginRight: '8px' }}>[cli]</a>
                <a href={`https://www.weather.gov/wrh/timeseries?site=${cityData.station_id}`} target="_blank" rel="noopener noreferrer" style={{ color: '#ffc658', textDecoration: 'none' }}>[ts]</a>
                </span>
            )}
            <span className="tz-label" style={{ marginLeft: '10px' }}>({cityData.timezone})</span>
            <button 
                onClick={handleRefresh} 
                disabled={isRefreshing}
                title="Force Refresh City Data"
                style={{
                    marginLeft: 'auto', 
                    background: 'transparent', 
                    border: 'none', 
                    color: isRefreshing ? '#666' : '#888', 
                    cursor: isRefreshing ? 'wait' : 'pointer',
                    fontSize: '1.2rem',
                    padding: '0 5px'
                }}
            >
                {isRefreshing ? '...' : ''}
            </button>
          </div>
      </h3>
      <div style={{ width: '100%', height: 350 }}>
        <ResponsiveContainer>
          <LineChart data={allData} margin={{ top: 10, right: 10, left: 0, bottom: 0 }}>
            <CartesianGrid strokeDasharray="3 3" stroke="#444" vertical={false} />
            <ReferenceArea x1={startTime} x2={midnightTime} fill="#ffffff" fillOpacity={0.05} />
            <ReferenceLine x={midnightTime} stroke="#888" strokeDasharray="5 5" label={{ value: "Today", position: 'insideTopRight', fill: '#aaa', fontSize: 12 }} />
            <ReferenceLine x={startOfTomorrow} stroke="#888" strokeDasharray="5 5" label={{ value: "Tomorrow", position: 'insideTopRight', fill: '#aaa', fontSize: 12 }} />
            
            {visibleKalshiLines.map((ref, i) => (
                <ReferenceLine 
                    key={i} 
                    segment={ref.segment} 
                    stroke={ref.stroke} 
                    strokeOpacity={ref.strokeOpacity}
                    strokeWidth={ref.strokeWidth}
                    strokeDasharray={ref.strokeDasharray}
                    label={{ 
                        value: ref.label, 
                        position: ref.labelPos, 
                        fill: ref.labelColor || ref.stroke, 
                        fontSize: ref.labelSize,
                        fillOpacity: ref.strokeOpacity 
                    }} 
                />
            ))}

            <XAxis dataKey="time" domain={['dataMin', 'dataMax']} type="number" tickFormatter={formatTick} stroke="#888" tick={{ fontSize: 11 }} interval="preserveStartEnd" minTickGap={40} />
            <YAxis domain={[minTemp, maxTemp]} stroke="#888" tick={{ fontSize: 11 }} width={35} />
            <Tooltip content={<CustomTooltip />} />
            <Line name="Actual (NWS)" type="monotone" dataKey="actual" stroke="#61dafb" dot={{ r: 2 }} activeDot={{ r: 5 }} strokeWidth={3} isAnimationActive={false} connectNulls />
            {models.map(model => (
                <Line key={model} name={`${model.toUpperCase()} Forecast`} type="monotone" dataKey={model} stroke={modelColors[model] || "#ccc"} dot={(props) => { if (props.payload.type === 'forecast') { return (<circle key={props.key} cx={props.cx} cy={props.cy} r={3} fill={props.stroke} stroke="none" />); } return <></>; }} strokeWidth={1} strokeDasharray="5 5" isAnimationActive={false} connectNulls />
            ))}
            <Brush
              dataKey="time"
              height={30}
              stroke="#888"
              tickFormatter={formatTick}
              fill="#282c34"
              startIndex={brushIndexes.startIndex}
              endIndex={brushIndexes.endIndex}
              onChange={(newRange) => {
                if (
                  newRange &&
                  newRange.startIndex !== undefined &&
                  newRange.endIndex !== undefined
                ) {
                  setBrushIndexes(newRange);
                }
              }}
              tick={{ fontSize: 10 }}
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
      <div className="stats">
        <span>Latest Actual: {latestActual}F {latestActualValue !== null ? nwsToCliRange(latestActualValue) : ''}</span>
      </div>
    </div>
  );
}

function SortableCityCard({ market, viewMode, onRefresh }) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
  } = useSortable({ id: market.city });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    // touchAction: 'none' // handled by dnd-kit usually, but good to keep in mind
  };

  return (
    <div ref={setNodeRef} style={style}>
      <CityCard 
        cityData={market} 
        viewMode={viewMode} 
        dragHandleProps={{ attributes, listeners }}
        onRefresh={onRefresh}
      />
    </div>
  );
}

function Dashboard() {
  const [weatherData, setWeatherData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [viewMode, setViewMode] = useState('today');
  
  const { user, logout } = useContext(AuthContext);
  const [monitors, setMonitors] = useState([]);
  const [openSidebarId, setOpenSidebarId] = useState(null);
  const [marketOrder, setMarketOrder] = useState([]);

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const fetchWeather = useCallback((force = false) => {
    if (force) setIsRefreshing(true);
    
    const url = force ? `${API_BASE}/api/weather/history?force_refresh=true` : `${API_BASE}/api/weather/history`;
    
    fetch(url)
      .then(response => response.json())
      .then(data => {
        if (data.status === 'loading') {
             // If we have markets (skeleton), show them!
             if (data.markets && data.markets.length > 0) {
                 setWeatherData(data);
                 setLoading(false); // Enable the UI to render
             }
             
             // Poll again in 2 seconds
             setTimeout(() => fetchWeather(false), 2000);
        } else {
            setWeatherData(data);
            setLoading(false);
            setIsRefreshing(false);
        }
      })
      .catch(error => {
        console.error('Error fetching data:', error);
        setLoading(false);
        setIsRefreshing(false);
      });
  }, []);

  const refreshCity = useCallback((city) => {
      // Call endpoint to refresh single city
      return fetch(`${API_BASE}/api/weather/history/${city}?force_refresh=true`)
          .then(res => res.json())
          .then(newCityData => {
              setWeatherData(prev => {
                  if (!prev || !prev.markets) return prev;
                  const newMarkets = prev.markets.map(m => m.city === city ? newCityData : m);
                  return { ...prev, markets: newMarkets, timestamp: Date.now() / 1000 };
              });
          })
          .catch(err => console.error(`Failed to refresh ${city}`, err));
  }, []);

  useEffect(() => {
    // Fetch weather data
    fetchWeather();
      
    // Fetch user monitors and settings
    if (user) {
        api.get('/api/user/monitors')
           .then(res => setMonitors(res.data))
           .catch(err => console.error(err));
           
        api.get('/api/user/settings')
           .then(res => {
               const orderSetting = res.data.find(s => s.key === 'chart_order');
               if (orderSetting) {
                   try {
                       setMarketOrder(JSON.parse(orderSetting.value));
                   } catch (e) {
                       console.error("Failed to parse chart_order", e);
                   }
               }
           })
           .catch(console.error);
    }
  }, [user]);

  const sortedMarkets = React.useMemo(() => {
    if (!weatherData?.markets) return [];
    if (marketOrder.length === 0) return weatherData.markets;
    
    const marketMap = new Map(weatherData.markets.map(m => [m.city, m]));
    const sorted = [];
    const seen = new Set();
    
    marketOrder.forEach(city => {
        if (marketMap.has(city)) {
            sorted.push(marketMap.get(city));
            seen.add(city);
        }
    });
    
    weatherData.markets.forEach(m => {
        if (!seen.has(m.city)) {
            sorted.push(m);
        }
    });
    
    return sorted;
  }, [weatherData, marketOrder]);

  const handleDragEnd = (event) => {
    const {active, over} = event;
    
    // Exit early if dropped outside droppable area
    if (!over) return;
    
    if (active.id !== over.id) {
      const oldIndex = sortedMarkets.findIndex(m => m.city === active.id);
      const newIndex = sortedMarkets.findIndex(m => m.city === over.id);
      
      const newSorted = arrayMove(sortedMarkets, oldIndex, newIndex);
      const newOrder = newSorted.map(m => m.city);
      
      setMarketOrder(newOrder); // Optimistic update
      
      if (user) {
          api.post('/api/user/settings', {
              key: 'chart_order',
              value: JSON.stringify(newOrder)
          }).catch(console.error);
      }
    }
  };

  const addSidebar = async () => {
    // Create new monitor in DB
    try {
        const newMonitor = {
            city_target: "Miami, FL",
            type: "cli",
            poll_interval: 30,
            name: "New Monitor"
        };
        const res = await api.post('/api/user/monitors', newMonitor);
        setMonitors([...monitors, res.data]);
        setOpenSidebarId(res.data.id);
    } catch (e) {
        console.error("Failed to add monitor", e);
    }
  };
  
  const updateMonitor = (updatedMonitor) => {
    api.put(`/api/user/monitors/${updatedMonitor.id}`, updatedMonitor)
       .then(res => {
           setMonitors(monitors.map(m => m.id === res.data.id ? res.data : m));
       })
       .catch(error => {
           console.error("Failed to update monitor", error);
           window.alert("Failed to update monitor. Please try again.");
       });
  };
  
  const deleteMonitor = (id) => {
    if(window.confirm("Remove this monitor?")) {
        api.delete(`/api/user/monitors/${id}`)
           .then(() => {
               setMonitors(monitors.filter(m => m.id !== id));
               if(openSidebarId === id) setOpenSidebarId(null);
           });
    }
  };

  const toggleSidebar = (id) => {
    setOpenSidebarId(openSidebarId === id ? null : id);
  };
  
  const isAnyOpen = openSidebarId !== null;

  return (
    <div className="App">
       <div style={{position: 'fixed', top: 10, left: 10, zIndex: 1000, color: 'white', background: 'rgba(0,0,0,0.5)', padding: '5px 10px', borderRadius: '4px'}}>
            User: {user?.username} <button onClick={logout} style={{marginLeft: '10px', cursor: 'pointer'}}>Logout</button>
       </div>

      {monitors.map((m, idx) => (
        <div key={m.id}>
             <MonitorSidebar 
                markets={weatherData?.markets}
                isOpen={openSidebarId === m.id}
                onToggle={() => toggleSidebar(m.id)}
                index={idx}
                isAnyOpen={isAnyOpen}
                monitorData={m}
                onUpdate={updateMonitor}
                onDelete={deleteMonitor}
            />
        </div>
      ))}
      
      {/* Add Button */}
      <div 
        style={{
            position: 'fixed',
            right: 0,
            top: `${20 + (monitors.length * 60)}px`,
            width: '50px',
            height: '50px',
            backgroundColor: '#333',
            borderRadius: '10px 0 0 10px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            cursor: 'pointer',
            boxShadow: '-2px 2px 5px rgba(0,0,0,0.3)',
            zIndex: 999,
            transition: 'transform 0.3s ease-in-out',
            transform: isAnyOpen ? 'translateX(-350px)' : 'translateX(0)'
        }}
        onClick={addSidebar}
        title="Add New Monitor"
      >
          <div style={{ color: '#888', fontSize: '24px', fontWeight: 'bold' }}>+</div>
      </div>

      <header className="App-header">
        <div style={{ marginBottom: '20px', display: 'flex', alignItems: 'center', flexWrap: 'wrap' }}>
          <button onClick={() => setViewMode('today')} style={{ marginRight: '10px', padding: '8px 16px', cursor: 'pointer' }}>Today Only</button>
          <button onClick={() => setViewMode('tomorrow')} style={{ marginRight: '10px', padding: '8px 16px', cursor: 'pointer' }}>Today + Tomorrow</button>
          <button onClick={() => setViewMode('full')} style={{ marginRight: '10px', padding: '8px 16px', cursor: 'pointer' }}>3d Window</button>
          
          <div style={{flexGrow: 1}}></div>
          
        </div>

        {loading && <p>Loading weather data...</p>}
        
        {weatherData && (
          <div className="grid-container">
            <DndContext 
              sensors={sensors}
              collisionDetection={closestCenter}
              onDragEnd={handleDragEnd}
            >
              <SortableContext 
                items={sortedMarkets.map(m => m.city)}
                strategy={rectSortingStrategy}
              >
                {sortedMarkets.map((market) => (
                  <SortableCityCard 
                    key={market.city} 
                    market={market} 
                    viewMode={viewMode}
                    onRefresh={refreshCity} 
                  />
                ))}
              </SortableContext>
            </DndContext>
          </div>
        )}
      </header>
    </div>
  );
}

function Main() {
    const { user, loading } = useContext(AuthContext);
    if (loading) return <div style={{color: 'white', padding: '20px'}}>Loading...</div>;
    return user ? <Dashboard /> : <Auth />;
}

export default function App() {
    return (
        <AuthProvider>
            <Main />
        </AuthProvider>
    );
}
