import React, { useState, useContext } from 'react';
import { AuthContext } from '../context/AuthContext';

export default function Auth() {
    const [isLogin, setIsLogin] = useState(true);
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const [passwordError, setPasswordError] = useState('');
    const { login, register } = useContext(AuthContext);

    const validatePassword = (pwd) => {
        if (!isLogin && pwd.length > 0 && pwd.length < 8) {
            setPasswordError('Password must be at least 8 characters');
            return false;
        }
        setPasswordError('');
        return true;
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        
        // Validate password for registration
        if (!isLogin && !validatePassword(password)) {
            return;
        }
        
        try {
            if (isLogin) {
                await login(username, password);
            } else {
                await register(username, password);
            }
        } catch (err) {
            setError(err.response?.data?.detail || "Authentication failed");
        }
    };

    return (
        <div style={{ 
            padding: '40px', 
            maxWidth: '400px', 
            margin: '100px auto', 
            backgroundColor: '#282c34', 
            color: '#eee', 
            borderRadius: '12px',
            boxShadow: '0 8px 16px rgba(0,0,0,0.5)',
            border: '1px solid #444'
        }}>
            <h2 style={{marginTop: 0, marginBottom: '25px', textAlign: 'center', fontWeight: 'normal', letterSpacing: '1px'}}>
                {isLogin ? 'Welcome Back' : 'Create Account'}
            </h2>
            {error && <div style={{ 
                color: '#ff6b6b', 
                marginBottom: '20px', 
                padding: '10px', 
                backgroundColor: 'rgba(255, 107, 107, 0.1)', 
                borderRadius: '4px',
                border: '1px solid rgba(255, 107, 107, 0.2)',
                fontSize: '0.9em',
                textAlign: 'center'
            }}>{error}</div>}
            <form onSubmit={handleSubmit}>
                <div style={{ marginBottom: '20px' }}>
                    <label style={{ display: 'block', marginBottom: '8px', color: '#aaa', fontSize: '0.9em' }}>Username</label>
                    <input 
                        type="text" 
                        value={username} 
                        onChange={e => setUsername(e.target.value)}
                        style={{ 
                            width: '100%', 
                            padding: '12px', 
                            borderRadius: '6px', 
                            border: '1px solid #444', 
                            backgroundColor: '#1c2025', 
                            color: '#fff',
                            fontSize: '1rem',
                            boxSizing: 'border-box',
                            outline: 'none'
                        }}
                        onFocus={e => e.target.style.borderColor = '#4fa6ff'}
                        onBlur={e => e.target.style.borderColor = '#444'}
                        required
                    />
                </div>
                <div style={{ marginBottom: '25px' }}>
                    <label style={{ display: 'block', marginBottom: '8px', color: '#aaa', fontSize: '0.9em' }}>Password</label>
                    <input 
                        type="password" 
                        value={password} 
                        onChange={e => {
                            setPassword(e.target.value);
                            validatePassword(e.target.value);
                        }}
                        style={{ 
                            width: '100%', 
                            padding: '12px', 
                            borderRadius: '6px', 
                            border: `1px solid ${passwordError ? '#ff6b6b' : '#444'}`, 
                            backgroundColor: '#1c2025', 
                            color: '#fff',
                            fontSize: '1rem',
                            boxSizing: 'border-box',
                            outline: 'none'
                        }}
                        onFocus={e => e.target.style.borderColor = passwordError ? '#ff6b6b' : '#4fa6ff'}
                        onBlur={e => e.target.style.borderColor = passwordError ? '#ff6b6b' : '#444'}
                        required
                    />
                    {passwordError && <div style={{ 
                        color: '#ff6b6b', 
                        marginTop: '5px', 
                        fontSize: '0.85em'
                    }}>{passwordError}</div>}
                </div>
                <button type="submit" style={{ 
                    width: '100%', 
                    padding: '12px', 
                    backgroundColor: '#4fa6ff', 
                    color: 'white', 
                    border: 'none', 
                    borderRadius: '6px', 
                    cursor: 'pointer',
                    fontSize: '1rem',
                    fontWeight: 'bold',
                    transition: 'background-color 0.2s'
                }}
                onMouseOver={e => e.target.style.backgroundColor = '#3b8ddf'}
                onMouseOut={e => e.target.style.backgroundColor = '#4fa6ff'}
                >
                    {isLogin ? 'Log In' : 'Sign Up'}
                </button>
            </form>
            <div style={{ marginTop: '25px', textAlign: 'center', fontSize: '0.9em' }}>
                <span style={{ color: '#888' }}>{isLogin ? "Don't have an account?" : "Already have an account?"}</span>
                <button 
                    onClick={() => setIsLogin(!isLogin)} 
                    style={{ background: 'none', border: 'none', color: '#4fa6ff', cursor: 'pointer', marginLeft: '5px', fontSize: 'inherit' }}
                >
                    {isLogin ? 'Register' : 'Login'}
                </button>
            </div>
        </div>
    );
}
